.logo {
  height: 48px; /* Ajusta la altura */
  width: auto; /* Mantiene proporción */
  object-fit: contain; /* Evita deformación */
  display: block;
}
@media (max-width: 768px) {
  .logo {
    height: 40px; /* Más pequeño en móviles */
  }
}
/* ===== NAVBAR ===== */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background-color: #0b1623;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.navbar img {
  height: 48px;
}

.nav-links {
  display: flex;
  gap: 1.5rem;
}

.nav-links a {
  color: white;
  font-weight: 500;
}

.nav-links a:hover {
  color: #f97316;
}

/* ===== HERO ===== */
.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 4rem 2rem;
  background-color: #132238;
}

.hero-content {
  max-width: 800px;
  margin-bottom: 2rem;
}

.hero h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.hero p {
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
}

/* ===== ABOUT ===== */
.about {
  padding: 4rem 2rem;
  background-color: #0d1b2a;
  text-align: center;
}

.about img {
  margin-top: 2rem;
  border-radius: 8px;
}

/* ===== PRODUCTS ===== */
.products {
  padding: 4rem 2rem;
  background-color: #132238;
  text-align: center;
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.product-card {
  padding: 2rem;
  border-radius: 8px;
  color: white;
  text-align: left;
}

.product-card.orange {
  background-color: #f97316;
}

.product-card.green {
  background-color: #22c55e;
}

.product-card.yellow {
  background-color: #eab308;
  color: black;
}

.product-card h3 {
  margin-bottom: 1rem;
}

.product-card ul {
  padding-left: 1rem;
}

/* ===== CONTACT ===== */
.contact {
  padding: 4rem 2rem;
  background-color: #0d1b2a;
  text-align: center;
}

.contact form {
  max-width: 500px;
  margin: auto;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.contact input,
.contact select,
.contact textarea {
  padding: 0.75rem;
  border: none;
  border-radius: 6px;
}

.contact button {
  background-color: #f97316;
  color: white;
  padding: 0.75rem;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.contact button:hover {
  background-color: #ea580c;
}

/* ===== FOOTER ===== */
footer {
  background-color: #0b1623;
  text-align: center;
  padding: 1rem;
  font-size: 0.9rem;
}

footer a {
  color: #f97316;
}

footer a:hover {
  text-decoration: underline;
}
